import {makeStyles} from '@material-ui/core'
import CloseIcon from '@material-ui/icons/Close'
import {useEffect, useState} from 'react'
import {useLocation, useNavigate} from 'react-router-dom'
import logo from '../../asset/images/logo.png'
import menu from '../../asset/images/menu.png'
import {ROUTE} from '../../router/routes'
import DialogMenuHeader from './dialog'

const useStyles = makeStyles({
  container_header: {
    position: 'fixed',
    zIndex: 1300,
    top: 0,
    left: 0,
    right: 0,
    color: 'white',
    height: 120,
    padding: '0 130px',
    display: 'flex',
    justifyContent: 'space-between',
    alignItems: 'center',
    transition: '0.4s',
    '&>:nth-child(1)': {
      display: 'flex',
      width: '100%',
      alignItems: 'center',
      justifyContent: 'space-between',
      '&>:nth-child(1)': {
        fontFamily: 'Anton',
        fontStyle: 'normal',
        fontWeight: 600,
        fontSize: '28px',
        lineHeight: '66px',
        display: 'flex',
        alignItems: 'center',
        flexWrap: 'wrap',
        color: '#FFFFFF',
        cursor: 'pointer',
        '&>img': {
          width: '104px',
          height: '67px',
          marginRight: '1rem',
        },
      },
      '&>:nth-child(2)': {
        display: 'flex',
        width: '510px',
        alignItems: 'center',
        listStyle: 'none',
        justifyContent: 'space-between',
        '&>li': {
          fontWeight: 300,
          fontSize: '18px',
          display: 'flex',
          color: 'white',
          cursor: 'pointer',
        },
        '&>li:hover': {
          fontWeight: 700,
        },
      },
      '&>:nth-child(3)': {
        display: 'flex',
        justifyContent: 'center',
        alignItems: 'center',
        width: '162px',
        height: '48px',
        background: '#FFFFFF',
        borderRadius: '4px',
        fontWeight: 600,
        fontSize: '18px',
        color: '#000000',
      },
    },
    '&>:nth-child(2)': {
      display: 'none',
    },
  },

  '@media (max-width: 740px)': {
    container_header: {
      padding: '0 16px',
      height: '65px',
      '&>div:nth-child(1)': {
        display: 'none',
      },
      '&>div:nth-child(2)': {
        display: 'flex',
        width: '100%',
        alignItems: 'center',
        justifyContent: 'space-between',
        '&>:nth-child(1)': {
          fontFamily: 'Anton',
          fontStyle: 'normal',
          fontWeight: 600,
          fontSize: '18px',
          lineHeight: '36px',
          display: 'flex',
          alignItems: 'center',
          color: '#FFFFFF',
          cursor: 'pointer',
          '&>img': {
            width: '60px',
            height: '38.65px',
            marginRight: '8px',
          },
        },
        '&>img:nth-child(2)': {
          width: '24px',
          height: '24px',
        },
      },
    },
  },

  '@media (min-width: 740px) and (max-width: 1185px)': {
    container_header: {
      position: 'fixed',
      zIndex: 1300,
      top: 0,
      left: 0,
      right: 0,
      color: 'white',
      height: 65,
      padding: '0 50px',
      display: 'flex',
      justifyContent: 'space-between',
      alignItems: 'center',
      transition: '0.4s',
      '&>:nth-child(1)': {
        display: 'flex',
        width: '100%',
        alignItems: 'center',
        justifyContent: 'space-between',
        '&>:nth-child(1)': {
          fontFamily: 'Anton',
          fontStyle: 'normal',
          fontWeight: 600,
          fontSize: '20px',
          lineHeight: '50px',
          display: 'flex',
          alignItems: 'center',
          flexWrap: 'wrap',
          color: '#FFFFFF',
          cursor: 'pointer',
          '&>img': {
            width: '104px',
            height: '67px',
            marginRight: '1rem',
            display: 'none',
          },
        },
        '&>:nth-child(2)': {
          display: 'flex',
          width: '410px',
          alignItems: 'center',
          listStyle: 'none',
          justifyContent: 'space-between',
          '&>li': {
            fontWeight: 300,
            fontSize: '16px',
            display: 'flex',
            color: 'white',
            cursor: 'pointer',
          },
          '&>li:hover': {
            fontWeight: 700,
          },
        },
        '&>:nth-child(3)': {
          display: 'flex',
          justifyContent: 'center',
          alignItems: 'center',
          width: '162px',
          height: '48px',
          background: '#FFFFFF',
          borderRadius: '4px',
          fontWeight: 600,
          fontSize: '18px',
          color: '#000000',
        },
      },
      '&>:nth-child(2)': {
        display: 'none',
      },
    },
  },
})

const Header = () => {
  const classes = useStyles()
  const location = useLocation()
  const navigate = useNavigate()
  const [open, setOpen] = useState(false)
  const [scroll, setScroll] = useState(false)
  useEffect(() => {
    window.scrollTo(0, 0)
  }, [location.pathname])
  useEffect(() => {
    window.addEventListener('scroll', () => {
      if (window.scrollY > 0) {
        setScroll(true)
      } else {
        setScroll(false)
      }
    })
  }, [])
  return (
    <div
      className={classes.container_header}
      style={{background: scroll ? 'black' : 'none'}}
    >
      <div>
        <span onClick={() => navigate('/')}>
          <img src={logo} alt='' />
          
        </span>
        <ul>
          <li
            style={
              location.pathname === '/'
                ? {
                    fontWeight: 700,
                    borderBottom: '2px solid white',
                  }
                : {fontWeight: 400}
            }
            onClick={() => navigate(ROUTE.HOME)}
          >
            Home
          </li>
          <li
            style={
              location.pathname === '/intro'
                ? {
                    fontWeight: 700,
                    borderBottom: '2px solid white',
                  }
                : {fontWeight: 400}
            }
            onClick={() => navigate(ROUTE.INTRO)}
          >
            
          </li>
          <li
            style={
              location.pathname === '/portfolio'
                ? {
                    fontWeight: 700,
                    borderBottom: '2px solid white',
                  }
                : {fontWeight: 400}
            }
            onClick={() => navigate(ROUTE.PORTFOLIO)}
          >
            
          </li>
          <li
            style={
              location.pathname === '/estimate_calculation'
                ? {
                    fontWeight: 700,
                    borderBottom: '2px solid white',
                  }
                : {fontWeight: 400}
            }
            onClick={() => navigate(ROUTE.ESTIMATE_CALCULATION)}
          >
            
          </li>
          <li
            style={
              location.pathname === '/development_inquiry'
                ? {
                    fontWeight: 700,
                    borderBottom: '2px solid white',
                  }
                : {fontWeight: 400}
            }
            onClick={() => navigate(ROUTE.DEVELOPMENT_INQUIRY)}
          >
            
          </li>
        </ul>
        {/* <span> </span> */}
      </div>
      <div>
        <span onClick={() => navigate('/')}>
          {' '}
          <img src={logo} alt='' />
          
        </span>
        {open ? (
          <span onClick={() => setOpen(false)}>
            <CloseIcon />
          </span>
        ) : (
          <img src={menu} alt='' onClick={() => setOpen(true)} />
        )}
      </div>
      <DialogMenuHeader open={open} setOpen={() => setOpen(false)} />
    </div>
  )
}

export default Header
